

export function makePdf(exportOrPrint, pdfDocDefinition) {

    import('pdfmake/build/pdfmake.js')
        .then((pdfMake) => {
            // pdfMake was loded
            console.log("pdfMake loded");
            var docDefinition = {
                compress: true,
                content: pdfDocDefinition
            };

            import('pdfmake/build/vfs_fonts.js')
                .then((pdfFonts) => {
                    pdfMake.vfs = pdfFonts.pdfMake.vfs;
                    pdfMake.tableLayouts = {
                        exampleLayout: {
                            hLineWidth: function (i, node) {
                                if (i === 0 || i === node.table.body.length) {
                                    return 0;
                                }
                                return (i === node.table.headerRows) ? 2 : 1;
                            },
                            vLineWidth: function (i) {
                                return 0;
                            },
                            hLineColor: function (i) {
                                return i === 1 ? 'black' : '#aaa';
                            },
                            paddingLeft: function (i) {
                                return i === 0 ? 0 : 8;
                            },
                            paddingRight: function (i, node) {
                                return (i === node.table.widths.length - 1) ? 0 : 8;
                            }
                        }
                    };

                    // download the PDF
                    if (exportOrPrint === 'export') {
                        pdfMake.createPdf(docDefinition).download('NTCL-MonitoringData.pdf');
                    }
                    if (exportOrPrint === 'print') {
                        pdfMake.createPdf(docDefinition).print();
                    }
                })
                .catch(err => {
                    console.log(err + "Error importing pdfFonts");
                });
        })
        .catch(err => {
            // Handle failure
            console.log(err + "Error importing pdfMake");
        });
};